import $ from 'jquery';
const $dropMenuItem = $('#drop-menu-item');
const $dropMenuContent = $('#drop-menu-content');
const $dropMenuLangItem = $('#drop-menu-lang-item');
const $dropContentLangItem = $('#drop-menu-content-item');
const $dropMenuIcon = $('.drop-menu-icon');

$($dropMenuItem).on('click', () => {
    $dropMenuIcon.toggleClass('drop-menu-icon-active')
    $dropMenuContent.toggleClass('header-navigation-drop-lang-active');
});

$($dropMenuLangItem).on('click', () => {
    $dropContentLangItem.toggleClass('header-navigation-drop-lang-active');
});
