import $ from 'jquery';
import 'slick-carousel/slick/slick.min';

$('.tabs-navigation-list__item').on('click', function() {
    $(this)
        .addClass('tabs-navigation-list__item-active')
        .siblings()
        .removeClass('tabs-navigation-list__item-active')
        .closest('div.tabs')
        .find('div.tabs-content')
        .removeClass('tabs-content-active')
        .eq($(this).index())
        .addClass('tabs-content-active');
});

$('.mob-team-slider').slick({
    slidesToShow: 1,
    slidesToScroll: 1,
    infinite: false,
    fade: true,
    prevArrow: '<button class="btn slider-btn-left"><img src="../../../img/btn-slider-left.svg" alt=""></button>',
    nextArrow: '<button class="btn slider-btn-right"><img src="../../../img/btn-slider-right.svg" alt=""></button>'
});
